<template>
    <div style="padding:15px">
        <el-row style="margin-bottom:10px">
            <el-button type="primary" @click="handleAdd">新增</el-button>
            <el-button type="primary" @click="handleEdit">修改</el-button>
            <el-button type="primary" @click="handleDel">删除</el-button>
            <el-button type="primary" @click="handleRefresh">刷新</el-button>
        </el-row>
        <el-table
            :data="tableData"
            :border=true
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectChange"
            type="index"
            v-loading="tableLoading"
        >
            <el-table-column type="selection" width="55">
            </el-table-column>
            <template v-for="(item) in tableHeader">
                <el-table-column v-bind:prop="item.name" v-bind:label="item.label" v-bind:width="item.width" v-bind:type="item.name=='index'?'index':''" :resizable=true>
                </el-table-column>
            </template>
        </el-table>
        <el-pagination
            style="margin-top:10px"
            :total="13"
            :page-sizes="[5,10,15]"
            layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="1"
            :page-size="10"
        >
        </el-pagination>
        <user-table-add ref="add" v-on:onSubmit="handleSubmit"></user-table-add>
        <user-table-edit ref="edit" v-on:onSubmit="handleSubmit"></user-table-edit>
    </div>
</template>
<script>
    import userTableAdd from './component/userTableAdd';
    import userTableEdit from './component/userTableEdit';
    export default {
        components:{
            userTableAdd,
            userTableEdit
        },
        data(){
            return {
                tableHeader:[
                    {label:'#',name:'index',width:'80'},
                    {label:'姓名',name:'name'},
                    {label:'性别',name:'sex'},
                    {label:'年龄',name:'age'},
                    {label:'电话',name:'tel'},
                    {label:'日期',name:'date'},
                    {label:'地址',name:'address'}
                ],
                tableData:[],
                selectDate:[],
                tableLoading:false,
                delLoading:false
            }
        },
        mounted:function(){
            this.handleRefresh()
        },
        methods:{
            handleSelectChange:function(selection){
                this.selectDate = selection;
            },
            //新增弹框
            handleAdd:function(){
                this.$refs.add.onOpen()
            },
            //新增、修改提交成功
            handleSubmit:function(state){
                if(state=='success'){
                    //刷新表格
                    this.handleRefresh()
                }
            },
            //修改弹框
            handleEdit:function(){
                if(this.selectDate.length == 1){
                    this.$refs.edit.onOpen(this.selectDate[0])
                }else{
                    this.$message({message: '请选择一行数据',type: 'warning'});
                }
            },
            //删除
            handleDel:function(){
                if(this.selectDate.length == 0){
                    this.$message({message: '请至少选择一行数据',type: 'warning'});
                    return false;
                }
                let self = this;
                this.$confirm('确定要删除选中数据？', '删除', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    closeOnClickModal:false,
                    beforeClose:(action,instance,done)=>{
                        if(action == 'confirm'){
                            //发送删除请求
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(function(){
                                instance.confirmButtonLoading = false
                                self.$message({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                                self.handleRefresh()
                                done();
                            },1000)
                        }else{
                            done();
                        }
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            },
            //刷新
            handleRefresh:function(){
                //发起后台请求，将请求的数据加载到表格中
                let self = this;
                this.tableLoading = true;
                setTimeout(function(){
                    self.tableData = [
                        {
                            date: '2016-05-03',
                            name: '王小虎',
                            address: '上海市普陀区金沙江路 1518 弄',
                            sex:'男',
                            age:18,
                            tel:13898980098
                        },
                        {
                            date: '2016-05-03',
                            name: '王红',
                            address: '上海市普陀区金沙江路 1518 弄',
                            sex:'女',
                            age:23,
                            tel:13898980098
                        },
                        {
                            date: '2016-05-03',
                            name: '李虎',
                            address: '内蒙古鄂尔多斯',
                            sex:'男',
                            age:43,
                            tel:13898980098
                        },
                        {
                            date: '2016-05-03',
                            name: '张三',
                            address: '上海市昌平区望京路 1518 弄',
                            sex:'女',
                            age:18,
                            tel:13898980098
                        },
                        {
                            date: '2016-05-03',
                            name: '李四',
                            address: '北京市朝阳区',
                            sex:'男',
                            age:70,
                            tel:13898980098
                        },
                        {
                            date: '2016-05-03',
                            name: '赵六',
                            address: '哈尔滨市12路3街',
                            sex:'男',
                            age:21,
                            tel:13898980098
                        },
                    ]
                    self.tableLoading = false
                },1000) 
            },
            //分页
            handleSizeChange:function(size){
                console.log('size',size)
            },
            handleCurrentChange:function(current){
                console.log('current',current)
            }
        },
    }
</script>




