<template>
    <div class="user-table-add">
        <el-dialog title="新增" 
            :visible.sync="dialogVisible" 
            width="33%" 
            :close-on-click-modal="false"
            :show-message=false
            @close="onClose"
        >
            <el-form ref="form" :model="form" label-width="120px" :rules="rules" :validate-on-rule-change=false>
                <el-form-item label="姓名：" prop="name">
                    <el-input class="inputStyle" v-model="form.name" placeholder="请输入姓名" clearable></el-input>
                </el-form-item>
                <el-form-item label="性别：" prop="sex">
                    <el-select class="inputStyle" v-model="form.sex" placeholder="请选择性别" clearable>
                        <el-option label="男" value="男"></el-option>
                        <el-option label="女" value="女"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="年龄：" prop="age">
                    <el-input class="inputStyle" v-model="form.age" clearable placeholder="请输入年龄"></el-input>
                </el-form-item>
                <el-form-item label="电话号码：" prop="tel">
                    <el-input class="inputStyle" v-model="form.tel" clearable placeholder="请输入电话号码"></el-input>
                </el-form-item>
                <el-form-item label="日期：" prop="date">
                    <el-date-picker class="inputStyle" v-model="form.date" type="date"  placeholder="请选择日期" clearable></el-date-picker>
                </el-form-item>
                <el-form-item label="地址：" prop="address">
                    <el-input class="inputStyle" v-model="form.address" clearable  placeholder="请输入地址"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="onClose">取 消</el-button>
                <el-button type="primary" @click="onSubmit" :loading="loading">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<style scoped lang="less">
    @import "../userTable.less";
</style>
<script>
    export default {
        name:'user-table-add',
        data() {
            return {
                form: {
                    date:'',
                    name: '',
                    address: '',
                    sex: '',
                    age: '',
                    tel: ''
                },
                dialogVisible:false,
                loading:false,
                rules: {
                    name: [
                        { required: true, message: '输入姓名', trigger: 'change' },
                        { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'change' }
                    ],
                    sex: [
                        { required: true, message: '请选择性别', trigger: 'change' }
                    ],
                    age: [
                        {require: true, validator: function(rule,value,callback){
                            var reg = /^\d+$/; // 非负整数
                            if(!reg.test(value)){
                                return callback(new Error('请输入正确的年龄'))
                            }
                            callback()
                        },trigger:'change'}
                    ],
                    tel: [
                        {required: true, validator: function (rule, value, callback) {
                            var reg = /^1[34578]\d{9}$/;
                            if (!value || !reg.test(value)) {
                                return callback(new Error('需要一个合法的手机号码'));
                            }
                            callback();
                        }, trigger: 'blur'}
                    ],
                    date: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    address: [
                        { required: true, message: '请输入地址', trigger: 'change' },
                        { min: 2, message: '长度至少为2个字符', trigger: 'change' }
                    ]
                }
            }
        },
        methods: {
            //提交
            onSubmit() {
                let self = this
                this.$refs.form.validate((valid) => {
                    if (valid) {//验证通过，发起后台请求
                        self.loading = true;
                        console.log('请求数据',this.form)
                        setTimeout(function(){
                            //发起请求，成功后
                            self.dialogVisible = false;
                            self.loading = false;
                            self.$emit('onSubmit','success');
                            self.$message({
                                message: '请求成功',
                                type: 'success'
                            });
                        },1000)
                    } else {
                        return false;
                    }
                });
            },
            onOpen(){
                this.loading = false;
                this.dialogVisible = true
            },
            onClose(){
                this.$refs.form.resetFields();
                this.loading = false
                this.dialogVisible = false
            }
        }
    }
</script>



